// Dynamic loading of all 2025 sponsor images so new files auto-appear.
// CRA (webpack) supports require.context.
interface WebpackContext {
  keys(): string[];
  (id: string): { default: string };
}

declare const require: {
  context(path: string, deep?: boolean, filter?: RegExp): WebpackContext;
};

export interface Sponsor {
  name: string;
  imgPath: string;
  website?: string; // optional future enhancement per sponsor
}

const sponsors2025Context = require.context('./img/2025', false, /(png|jpe?g|svg)$/i);

export const SPONSORS_2025: Sponsor[] = sponsors2025Context.keys().map((k) => {
  const fileName = k.replace('./', '');
  const baseName = fileName.replace(/\.(png|jpe?g|svg)$/i, '');
  // Turn file name like 'Logo-Poly' into readable 'Logo Poly'
  const displayName = baseName
    .replace(/[-_]+/g, ' ')
    .replace(/\b(logo)\b/i, '')
    .replace(/\s{2,}/g, ' ') // collapse double spaces
    .trim()
    .replace(/\b\w/g, (c) => c.toUpperCase());
  return {
    name: displayName || baseName,
    imgPath: sponsors2025Context(k).default,
  } as Sponsor;
});

// (Optional) Sort alphabetically for consistent layout
SPONSORS_2025.sort((a, b) => a.name.localeCompare(b.name));
